import { _decorator, Component, Node, Prefab, Vec3, instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GameCtrl')
export class GameCtrl extends Component {
    @property(Prefab)
    public enemyWave1: Prefab [] = [];
    @property(Prefab)
    public enemyWave2: Prefab [] = [];
    @property(Prefab)
    public enemyWave3: Prefab [] = [];
    start() {
        this.Turn1_1();
    }

    update(deltaTime: number) {
        
    }
    Turn1_1(){
        console.log('turn 1');

        let enemyPre = this.enemyWave1[0];

        let enemyPos1 = new Vec3(-220, 415, 0);
        let enemyPos2 = new Vec3(-110, 415, 0);
        let enemyPos3 = new Vec3(0, 415, 0);
        let enemyPos4 = new Vec3(110, 415, 0);
        let enemyPos5 = new Vec3(220, 415, 0);

        let prefabInstance1 = instantiate(enemyPre);
        let prefabInstance2 = instantiate(enemyPre);
        let prefabInstance3 = instantiate(enemyPre);
        let prefabInstance4 = instantiate(enemyPre);
        let prefabInstance5 = instantiate(enemyPre);

        prefabInstance1.setPosition(enemyPos1);
        prefabInstance2.setPosition(enemyPos2);
        prefabInstance3.setPosition(enemyPos3);
        prefabInstance4.setPosition(enemyPos4);
        prefabInstance5.setPosition(enemyPos5);

        this.node.addChild(prefabInstance1);
        this.node.addChild(prefabInstance2);
        this.node.addChild(prefabInstance3);
        this.node.addChild(prefabInstance4);
        this.node.addChild(prefabInstance5);
    }
}


